@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  box-sizing: border-box;
}

/* placeholder text */

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: lightgray;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: lightgray;
  opacity: 1; /* Firefox */
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: lightgray;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: lightgray;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: lightgray;
}

/* end of placeholder text change */

body{
  font-family: 'Roboto', sans-serif;
  text-align: center;
  margin: 0px;
  height: 100vh;
  min-height: 100vh;
}

#pageContainer{
  position: relative;
  min-height: 100vh;
}




#titleHolder{
  width: 100%;
  background-color: rgb(34,34,34);
  color: rgb(102, 216, 250);
  padding: 15px;
 
}


#projectPageTitle{
  font-size: 50px;
}

#projectPageTitle2{
  margin-top: 10px;
  font-size: 25px;
}


#searchBar{
  margin: 40px auto;
  border-radius: 5px;
  width: 80vw;
  height: 80px;
  font-size: 40px;
  box-sizing: border-box;
  padding: 15px;
  outline: none;
  border: 1px solid rgb(34,34,34);
}

#searchBar:focus{
  box-shadow: 0px 0px 10px 2px lightgray;
}

#searchBar:focus::-webkit-input-placeholder{
  color: transparent;
}

#searchBar:focus:-ms-input-placeholder{
  color: transparent;
}

#searchBar:focus::placeholder{
  color: transparent;
}

.fa-search{
  transform: scale(2.5);
  position: relative;
  right: 50px;
  bottom: 10px;
}

/* start of the thumbnails code */

#container{
  margin: 30px auto;
  width: 1300px;
 
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  align-items: start;
}



.holderBox{
  border: 3px solid gray;
  padding: 30px;
  text-align: left;
  border-radius: 10px;
  box-shadow: 5px 5px 10px 5px lightgray;
  height: 400px;
  }

  .holderBox img{
    width: 200px;
    height: 150px;
    display: block;
    margin: auto;
    margin-bottom: 2em;
  }
  

  .holderBox a{
    text-decoration: none;
    color: inherit;
  }


.holderBox:hover{
  transform: scale(1.015);
  transition: .5s;
  box-shadow: 0px 0px 10px 8px lightgray;
 
}


#contentWrapper{
  padding-bottom: 189px;
}

#footerMargin{
  margin-top: 60px;
}

footer{

  position: absolute;
  bottom: 0;
 
  width: 100%;
  height: 189px;


  background-color: red;

  padding: 25px;
  color: white;

  font-size: 22px;
  line-height: 10px;
}

@media (max-width: 1024px){

  #container{
    width: 800px;
    grid-template-columns: 1fr 1fr;
  }


  /* end of 1024 media */
}

@media (max-width: 768px){
  #container{
    width: 600px;
    grid-template-columns: 1fr 1fr;
  }

  .holderBox{
    height: 450px;
  }

  #searchBar{
    height: 40px;
    font-size: 20px;
  }

  .fa-search{
    transform: scale(1.3);
    bottom: 2px;
    right: 40px;
  }
  /* end of 768 */
}

@media (max-width: 425px){

  #container{
    width: 325px;
    grid-template-columns: 1fr;
  }

  
  #projectPageTitle{
    font-size: 20px;
  }

  #projectPageTitle2{
    font-size: 13px;
  }

  #searchBar{
    margin: 5px auto;
    font-size: 15px;
  }



/* end of 425 */
}


@media (max-width: 320px){

  #container{
    width: 275px;
     }

     .holderBox{
       height: auto;
     }

     .holderBox h2, p{
       font-size: 15px;
     }

 

  /* end of 320 */
}


  


